.todos {
  width: 100%;
  background: #FFFFFF;
  padding: 20px;
}

.buttonSorter {
  display: block;
  width: 100px;
  height: 40px;
  margin: 20px auto;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid rgba(0,0,0, .5);
  background-color: rgba(14, 162, 225, 0.5);
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 10px rgba(0,0,0, .2);
    background-color: rgba(14, 193, 225, 0.8);
  }
}

.todosList {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
  height: 700px;
  overflow-y: auto;
}
